import React from "react";

import "./styles/Layout_Pokemon.css";
import PokemonCard from "./PokemonCard";
import PageLoading from "./PageLoading";

class Layout_Pokemon extends React.Component {
  state = {
    nextPage: 0,
    loading: null,
    error: null,
    datos: {
      results: [],
    },
  };
  componentDidMount() {
    this.fetchData(2);
  }

  fetchData = async (page) => {
    if (page === 2) {
      this.state = {
        loading: true,
        error: null,
        nextPage: this.state.nextPage,
        datos: {
          results: [],
        },
      };
      try {
        const response = await fetch(
          `https://pokeapi.co/api/v2/pokemon/?offset=${this.state.nextPage}&limit=12,`
        );
        const data = await response.json();
        this.setState({
          loading: false,
          error: null,
          nextPage: this.state.nextPage,
          datos: data,
        });
      } catch (error) {
        this.setState({
          loading: false,
          error: error,
        });
      }
    }
    if (page === 1) {
      try {
        this.state = {
          loading: true,
          error: null,
          nextPage: this.state.nextPage + 12,
          datos: {
            results: [],
          },
        };
        const response = await fetch(
          `https://pokeapi.co/api/v2/pokemon/?offset=${this.state.nextPage}&limit=12,`
        );
        const data = await response.json();
        this.setState({
          loading: false,
          error: null,
          nextPage: this.state.nextPage,
          datos: data,
        });
      } catch (error) {
        this.setState({
          loading: false,
          error: error,
        });
      }
    }
    if (page === 0) {
      try {
        this.state = {
          loading: true,
          error: null,
          nextPage: this.state.nextPage - 12,
          datos: {
            results: [],
          },
        };
        const response = await fetch(
          `https://pokeapi.co/api/v2/pokemon/?offset=${this.state.nextPage}&limit=12,`
        );
        const data = await response.json();
        this.setState({
          loading: false,
          error: null,
          nextPage: this.state.nextPage,
          datos: data,
        });
      } catch (error) {
        this.setState({
          loading: false,
          error: error,
        });
      }
    }
  };
  render() {
    if (this.state.loading === true) {
      return (
        <div className="Layout__container__Pokemon">
          <PageLoading />;
        </div>
      );
    }
    return (
      <div className="Layout__container__Pokemon">
        <ul className="List__Pokemon">
          {this.state.datos.results.map((pokemon) => (
            <li className="List__Pokemon__Card" key={pokemon.name}>
              <PokemonCard pokemon={pokemon.name} />
            </li>
          ))}
        </ul>
        <div>
          <button
            onClick={() => this.fetchData(0)}
            className="Button_Prev_page"
          ></button>
          <button
            onClick={() => this.fetchData(1)}
            className="Button_Next_page"
          ></button>
        </div>
      </div>
    );
  }
}

export default Layout_Pokemon;
